.meal {
    display: flex;
    justify-content: space-between;
    margin: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ccc;
  }
  
  .meal h3 {
    text-align: left;
    margin: 0 0 0.25rem 0;
  }
  
  .description {
    text-align: left;

    font-style: italic;
  }
  
  .price {
    text-align: left;

    margin-top: 0.25rem;
    font-weight: bold;
    color: #a70e41;
    font-size: 1.25rem;
  }